import React, {useEffect, useState, useMemo} from "react";
import Movies from "../components/Movies";
import {useLocation, useNavigate, createSearchParams, useSearchParams} from "react-router-dom";
import {useForm} from "react-hook-form";
import Pagination from "../components/Pagination";
import "../_movies.scss";
import "../_search.scss";
import "../_pagination.scss";

function Search() {
    const [loading, setLoading] = useState(false);
    const genres = [
        "Action",
        "Adventure",
        "Animation",
        "Biography",
        "Comedy",
        "Crime",
        "Documentary",
        "Drama",
        "Family",
        "Fantasy",
        "Film-Noir",
        "History",
        "Horror",
        "Music",
        "Musical",
        "Mystery",
        "Romance",
        "Sci-Fi",
        "Short-Film",
        "Sport",
        "Superhero",
        "Thriller",
        "War",
        "Western"
    ]

    // useForm
    const {register, handleSubmit, getValues, setValue, formState: {errors}} = useForm();

    // URL     
    function useQuery() {
        const {search} = useLocation();
        return useMemo(() => new URLSearchParams(search), [search])
    }

    let query = useQuery();

    const getSearchResult = async () => {
        setLoading(true);
        let queryPair = {
            query_term: query.get("queryTerm"),
            genre: query.get("genre"),
            minimum_rating: query.get("minimumRating"),
            quality: query.get("quality"),
            sort_by: query.get("sortBy"),
            order_by: query.get("orderBy"),
            page: query.get("page"),
            width_rt_ratings: true,
            limit: pageSize
        };

        for (let i in queryPair) {
            queryPair[i] === null && delete queryPair[i]
        }

        let result = await (
            await fetch(`
            https://yts.mx/api/v2/list_movies.json?
            ${createSearchParams(queryPair).toString()}
            `)
        ).json();
        setSearchResult(result.data);
        setLoading(false);
    }

    // URL   query      form 
    const location = useLocation();
    const [searchParams] = useSearchParams();
    useEffect(() => {
        if (query.get("queryTerm")) {
            getSearchResult()
        }
        for (const entry of searchParams.entries()) {
            const [param, value] = entry;
            setValue(param, value);
        }
    }, [location.search])

    //   onSubmit
    const navigate = useNavigate();
    const onSubmit = async (data) => {
        setCurrentPage(1);
        data.page = 1;
        for (let i in data) {
            data[i] === '' && delete data[i]
        }
        navigate({
            // pathname: "search",
            search: createSearchParams(data).toString()
        });
    }

    // Pagination
    let pageSize = 20;
    const [currentPage, setCurrentPage] = useState(1);
    useEffect(() => {
        let currentParams = Object.fromEntries([...searchParams]);
        currentParams.page = currentPage;
        console.log(currentParams);
        navigate({search: createSearchParams(currentParams).toString()})
    }, [currentPage])

    //  
    const [searchResult, setSearchResult] = useState([]);
    useEffect(() => {
        window.scrollTo(0, 0);
    }, [searchResult])

    return (
        <>
            <section className="container">
                <div className="cover bg--gray flex__column flex__end">
                    <form onSubmit={handleSubmit(onSubmit)}
                          className={"search"}>
                        <label className={"search__label"}></label>
                        <input className={"search__input"}
                               {...register("queryTerm",
                                   {required: "() ."})} />
                        <label className={"search__label"}></label>
                        <select className={"search__input"}
                                {...register("genre")}>
                            <option value={""}> </option>
                            {genres.map((genre, id) =>
                                <option value={genre} key={id}>{genre}</option>
                            )}
                        </select>
                        <label className={"search__label"}> </label>
                        <select className={"search__input"}
                                {...register("minimumRating")}>
                            <option value=""> </option>
                            {[...Array(10)].map((_, i) => i)
                                .map((int, key) => (
                                    <option value={`${int}`} key={key}>{int}</option>
                                ))}
                        </select>
                        <label className={"search__label"}></label>
                        <select className={"search__input"}
                                {...register("quality")}>
                            <option value=""> </option>
                            <option value="720p">720p</option>
                            <option value="1080p">1080p</option>
                            <option value="2160p">2160p</option>
                            <option value="3D">3D</option>
                        </select>
                        <label className={"search__label"}> </label>
                        <select className={"search__input"}
                                {...register("sortBy")}>
                            <option value="date_added"> </option>
                            <option value="title"></option>
                            <option value="year"></option>
                            <option value="rating"></option>
                            <option value="peers"></option>
                            <option value="seeds"></option>
                            <option value="download_count"> </option>
                            <option value="like_count"> </option>
                        </select>
                        <label className={"search__label"}></label>
                        <select className={"search__input"}
                                {...register("orderBy")}>
                            <option value="desc"></option>
                            <option value="asc"></option>
                        </select>
                        <input type="submit" className={"search__submit bg--black"} value={""}/>
                    </form>
                    <h1> : {searchResult.movie_count
                        ? searchResult.movie_count : 0} </h1>
                </div>
                {errors.queryTerm
                    && <div className={"search__error flex__center bg--red"}>
                        {errors.queryTerm.message}</div>
                }
                <div className={"bg--blue flex__center"}>
                    <Pagination
                        className="pagination-bar"
                        currentPage={currentPage}
                        totalCount={searchResult.movie_count || 0}
                        pageSize={pageSize}
                        onPageChange={page => setCurrentPage(page)}
                    />
                </div>
                {loading
                    ? <h1 className="flex__center">Loading...</h1>
                    : searchResult.movie_count
                        ? <Movies movies={searchResult.movies}/>
                        : <h2 className={"flex__center"}>{"  !"}</h2>
                }
                <div className={"bg--blue flex__center"}>
                    <Pagination
                        className="pagination-bar"
                        currentPage={currentPage}
                        totalCount={searchResult.movie_count || 0}
                        pageSize={pageSize}
                        onPageChange={page => setCurrentPage(page)}
                    />
                </div>
            </section>
        </>

    )
}

export default Search;